// src/features/esop-history/components/EsopHistoryDashboard.jsx
"use client"

import { IconLoader2, IconRefresh } from "@tabler/icons-react"

import { Button } from "@/components/ui/button"
import { HistoryChartLegend, HistoryLineChart } from "./HistoryLineChart"
import { useEsopHistoryData } from "../hooks/useEsopHistoryData"

function SectionHeading({ title, description, action }) {
  return (
    <div className="flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-between">
      <div>
        <h2 className="text-base font-semibold text-foreground sm:text-lg">{title}</h2>
        {description ? <p className="text-sm text-muted-foreground">{description}</p> : null}
      </div>
      {action ?? null}
    </div>
  )
}

function TotalsSummary({ totals = [] }) {
  const latest = totals.at(-1)
  const previous = totals.at(-2)
  const trend = previous && latest ? latest.esopCount - previous.esopCount : null
  return (
    <div className="flex flex-col gap-4 rounded-lg border border-border bg-background p-4">
      <div>
        <p className="text-sm font-medium uppercase tracking-wide text-muted-foreground">Daily totals</p>
        <p className="text-3xl font-bold text-foreground">{latest ? latest.esopCount : "–"}</p>
        <p className="text-sm text-muted-foreground">최근 집계된 ESOP 진행 건수</p>
      </div>
      <div className="rounded-md border border-dashed border-muted-foreground/40 p-3 text-sm text-muted-foreground">
        <p>최근 기준일: {latest ? latest.date : "–"}</p>
        <p>Send Jira: {latest ? latest.sendJiraCount : "–"}</p>
        <p>
          전일 대비: {trend === null ? "–" : trend > 0 ? `+${trend}` : trend}
        </p>
      </div>
    </div>
  )
}

function TotalsChart({ totals = [] }) {
  const series = [
    { name: "ESOP 진행", data: totals.map((point) => ({ date: point.date, value: point.esopCount })) },
    { name: "Send Jira", data: totals.map((point) => ({ date: point.date, value: point.sendJiraCount })) },
  ]

  return (
    <div className="flex flex-col gap-4 rounded-lg border border-border bg-background p-4">
      <SectionHeading
        title="일별 ESOP 진행 현황"
        description="최근 3주간의 ESOP 진행 및 Jira 전송 추세"
      />
      <HistoryLineChart series={series} height={260} />
      <HistoryChartLegend series={series} />
    </div>
  )
}

function BreakdownSection({ breakdown }) {
  const series = Array.isArray(breakdown?.series) ? breakdown.series : []
  return (
    <div className="flex flex-col gap-4 rounded-lg border border-border bg-background p-4">
      <SectionHeading
        title={`${breakdown.label} 트렌드`}
        description="일별 진행량을 카테고리별로 비교"
      />
      <HistoryLineChart series={series} height={240} />
      <HistoryChartLegend series={series} />
    </div>
  )
}

export function EsopHistoryDashboard({ lineId }) {
  const { status, payload, refresh } = useEsopHistoryData(lineId)
  const isLoading = status.isLoading
  const error = status.error

  return (
    <div className="flex h-full flex-col gap-6 overflow-y-auto p-4">
      <header className="flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-semibold tracking-tight">ESOP History · {lineId}</h1>
          <p className="text-sm text-muted-foreground">Daily ESOP 진행 현황과 Jira 전송량을 추적합니다.</p>
        </div>
        <Button type="button" variant="outline" onClick={refresh} disabled={isLoading} className="gap-2">
          <IconRefresh className="h-4 w-4" /> 새로고침
        </Button>
      </header>

      {isLoading ? (
        <div className="flex flex-1 items-center justify-center">
          <div className="flex items-center gap-2 text-muted-foreground">
            <IconLoader2 className="h-5 w-5 animate-spin" /> 데이터를 불러오는 중…
          </div>
        </div>
      ) : error ? (
        <div className="flex flex-1 items-center justify-center">
          <div className="rounded-md border border-destructive/50 bg-destructive/10 px-4 py-3 text-sm text-destructive">
            {error}
          </div>
        </div>
      ) : payload ? (
        <div className="flex flex-col gap-6">
          <div className="grid gap-4 lg:grid-cols-[280px_1fr]">
            <TotalsSummary totals={payload.totals} />
            <TotalsChart totals={payload.totals} />
          </div>
          <SectionHeading
            title="카테고리별 추세"
            description="SDWT, 설비, 샘플 타입 등 다양한 관점에서 라인 활동량을 비교"
          />
          <div className="grid gap-4 xl:grid-cols-2">
            {payload.breakdowns.map((breakdown) => (
              <BreakdownSection key={breakdown.key} breakdown={breakdown} />
            ))}
          </div>
        </div>
      ) : (
        <div className="flex flex-1 items-center justify-center text-sm text-muted-foreground">
          표시할 데이터가 없습니다.
        </div>
      )}
    </div>
  )
}
