// src/features/esop-history/components/HistoryLineChart.jsx
"use client"

import * as React from "react"

import { cn } from "@/lib/utils"

const COLORS = [
  "var(--chart-1)",
  "var(--chart-2)",
  "var(--chart-3)",
  "var(--chart-4)",
  "var(--chart-5)",
  "var(--color-slate-11)",
  "var(--color-plum-11)",
]

const DATE_FORMATTER = new Intl.DateTimeFormat("ko-KR", {
  month: "2-digit",
  day: "2-digit",
})

const NUMBER_FORMATTER = new Intl.NumberFormat("en-US")

function buildPath(points, xScale, yScale) {
  if (points.length === 0) return ""
  return points
    .map((point, index) => {
      const command = index === 0 ? "M" : "L"
      return `${command}${xScale(index)},${yScale(point.value)}`
    })
    .join(" ")
}

export function HistoryLineChart({
  series = [],
  className,
  height = 220,
  xLabels,
  yTicks = 4,
}) {
  const labels = xLabels && xLabels.length > 0 ? xLabels : series?.[0]?.data.map((point) => point.date) ?? []
  const maxValue = React.useMemo(() => {
    let max = 0
    series.forEach(({ data = [] }) => {
      data.forEach((point) => {
        if (point.value > max) max = point.value
      })
    })
    return max
  }, [series])

  const width = 640
  const margin = { top: 24, right: 24, bottom: 40, left: 56 }
  const innerWidth = Math.max(width - margin.left - margin.right, 10)
  const innerHeight = Math.max(height - margin.top - margin.bottom, 10)
  const safeMax = maxValue > 0 ? maxValue : 1
  const tickCount = Math.max(yTicks, 2)

  const xScale = React.useCallback(
    (index) => {
      if (labels.length <= 1) return margin.left
      const ratio = index / (labels.length - 1)
      return margin.left + ratio * innerWidth
    },
    [labels.length, margin.left, innerWidth]
  )

  const yScale = React.useCallback(
    (value) => {
      const ratio = value / safeMax
      const clamped = Number.isFinite(ratio) ? Math.min(Math.max(ratio, 0), 1) : 0
      return margin.top + innerHeight - clamped * innerHeight
    },
    [safeMax, margin.top, innerHeight]
  )

  const gridLines = Array.from({ length: tickCount }, (_, index) => {
    const ratio = index / (tickCount - 1)
    const value = safeMax * ratio
    const y = yScale(value)
    return { id: index, value, y }
  })

  return (
    <div className={cn("relative", className)}>
      <svg viewBox={`0 0 ${width} ${height}`} className="h-full w-full text-muted-foreground">
        <rect
          x={margin.left}
          y={margin.top}
          width={innerWidth}
          height={innerHeight}
          fill="var(--color-muted)"
          rx={8}
        />
        {gridLines.map((tick) => (
          <g key={`grid-${tick.id}`}>
            <line
              x1={margin.left}
              x2={margin.left + innerWidth}
              y1={tick.y}
              y2={tick.y}
              stroke="currentColor"
              strokeDasharray="4 4"
              opacity={tick.id === tickCount - 1 ? 0.4 : 0.2}
            />
            <text
              x={margin.left - 12}
              y={tick.y + 4}
              fontSize="11"
              textAnchor="end"
              fill="currentColor"
            >
              {NUMBER_FORMATTER.format(Math.round(tick.value))}
            </text>
          </g>
        ))}
        <line
          x1={margin.left}
          x2={margin.left + innerWidth}
          y1={margin.top + innerHeight}
          y2={margin.top + innerHeight}
          stroke="currentColor"
          strokeWidth="1.5"
        />
        {labels.map((label, index) => {
          const x = xScale(index)
          const date = new Date(label)
          return (
            <text
              key={label}
              x={x}
              y={height - 12}
              fontSize="11"
              textAnchor="middle"
              fill="currentColor"
            >
              {Number.isNaN(date.getTime()) ? label : DATE_FORMATTER.format(date)}
            </text>
          )
        })}
        {series.map((item, seriesIndex) => {
          const color = COLORS[seriesIndex % COLORS.length]
          const pathD = buildPath(item.data, xScale, yScale)
          return (
            <g key={item.name}>
              <path
                d={pathD}
                fill="none"
                stroke={color}
                strokeWidth={2.5}
                strokeLinejoin="round"
                strokeLinecap="round"
              />
              {item.data.map((point, pointIndex) => (
                <circle
                  key={`${item.name}-${point.date}`}
                  cx={xScale(pointIndex)}
                  cy={yScale(point.value)}
                  r={4}
                  fill={color}
                  stroke="var(--color-background)"
                  strokeWidth={1.5}
                />
              ))}
            </g>
          )
        })}
      </svg>
    </div>
  )
}

export function HistoryChartLegend({ series = [] }) {
  return (
    <div className="flex flex-wrap gap-x-4 gap-y-2 text-sm text-muted-foreground">
      {series.map((item, index) => (
        <div key={item.name} className="flex items-center gap-2">
          <span
            className="inline-block h-2.5 w-2.5 rounded-full"
            style={{ backgroundColor: COLORS[index % COLORS.length] }}
          />
          <span className="font-medium text-foreground">{item.name}</span>
        </div>
      ))}
    </div>
  )
}
