// src/features/esop-history/hooks/useEsopHistoryData.js
"use client"

import * as React from "react"

import { generateMockHistory } from "../utils/generateMockHistory"

const INITIAL_STATUS = { isLoading: false, error: null }

export function useEsopHistoryData(lineId) {
  const [status, setStatus] = React.useState(INITIAL_STATUS)
  const [payload, setPayload] = React.useState(null)
  const timeoutRef = React.useRef(null)

  const loadHistory = React.useCallback(() => {
    if (!lineId) {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current)
        timeoutRef.current = null
      }
      setPayload(null)
      setStatus(INITIAL_STATUS)
      return
    }

    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current)
      timeoutRef.current = null
    }

    setStatus({ isLoading: true, error: null })

    timeoutRef.current = setTimeout(() => {
      try {
        const data = generateMockHistory(lineId)
        setPayload(data)
        setStatus(INITIAL_STATUS)
        timeoutRef.current = null
      } catch (error) {
        const message = error instanceof Error ? error.message : "Failed to load history"
        setPayload(null)
        setStatus({ isLoading: false, error: message })
        timeoutRef.current = null
      }
    }, 240)
  }, [lineId])

  React.useEffect(() => {
    loadHistory()
    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current)
        timeoutRef.current = null
      }
    }
  }, [loadHistory])

  return React.useMemo(
    () => ({
      status,
      payload,
      refresh: loadHistory,
    }),
    [status, payload, loadHistory]
  )
}
